@import url(https://fonts.googleapis.com/css2?family=EB+Garamond:wght@400;600;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
    font-family: 'EB Garamond', serif;
}
.mainNavigation {
    margin: 2rem;
}

.navigationList {
    list-style-type: none;
}

.primary-header {
    align-items: center;
    justify-content: center;
    margin: 1rem;
}

.flex {
    display: flex;
    justify-content: flex-start;
    @media screen and (max-width: 768px) {
        justify-content: center;
    }
}

.themeButton {
    /* margin: 2rem; */
    border: none;
    background-color: transparent;
    position: absolute;
    margin-left: 95%;
    cursor: pointer;
}
.experiencesDiv h1 {
    display: flex;
    justify-content: center;
    vertical-align: top;
    text-align: center;
    font-size: 40px;
    margin-top: 10%;
}
.experiencesBlog {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-inline: auto;
    margin: 8%;
    text-align: left;
}


.experiencesBlogTitle {
    width: 300px;
    height: 210px;
}

/*need to add responsiveness for iphone so it is consistent throughout (the title)*/



.iconStyle {
    padding-right: 1.5%;
}
.techIconStyle {
    padding-left: 17%;
}

